<script>

    export default {
        name: "Avatar",
        props: {
            initialWidth: {
                type: Number,
                required: true
            },
            textBubble: {
                //type: String,
                default: "bottom"
            }
        },
        data: function() {
            return {
                curLanguage: "en",
                isReady: false,
                isTalking: false,
                isListening: false,
                curSpeech: "",
                //width: this.initialWidth,
                width: screen.width,
                bubbleText: "Hi, I’m Julia part of MirageVision Outdoor’s TV team.",
                promise: null,
                tele: false,
                looking: false,
                dialogImg: false,
                dialogVdo: false
            }
        },
        computed: {
            height: {
                get: function () {
                    return this.width * 1.778;
                },
                set: function (newVal) {
                    //this.width = newVal * 8/9;
                    this.width = newVal;
                }
            },
            spVoice: function() {
                switch (this.curLanguage) {
                    case 'es':
                        return 4;
                    default: // en
                        return 3;
                }
            },
            spLang: function() {
                switch (this.curLanguage) {
                    case 'es':
                        return 2;
                    default: // en
                        return 1;
                }
            },
            spEngine: function() {
                switch (this.curLanguage) {
                    case 'es':
                        return 4;
                    default: // en
                        return 3;
                }
            },
            textBubbleClass: function () {
                if (this.textBubble === 'bottom') {
                    return "bottom-bubble"
                }
                else if (this.textBubble === 'left') {
                    return ["side-bubble", "left-bubble"]
                }
                else if (this.textBubble === 'right') {
                    return ["side-bubble", "right-bubble"]
                }
                return ""
            },


        },
        watch: {
            width: function () {
                //window.dynamicResize(this.width * 1.5, this.height);
                window.dynamicResize(this.width, this.height);
                //this.tele = clientApp.tele;
            },
            curSpeech: function () {
                this.bubbleText = this.curSpeech;
            },
        },
        methods: {
            preloadSpeech: function(speechText) {
                window.loadText(speechText, this.spVoice, this.spLang, this.spEngine);
            },
            speak: function(speakText) {
                this.curSpeech = speakText;

                if (this.isTalking && this.promise) {
                    this.promise.resolve({interrupted : true});
                    this.promise = null;
                    window.stopSpeech();
                }

                const that = this;
                 //this.promise = new DeferredPromise();
                if (this.curLanguage === 'en') {
                    window.sayText('<prosody rate="+20%">' + speakText + '</prosody>', that.spVoice, that.spLang, that.spEngine);
                }
                else {
                    window.sayText(speakText, that.spVoice, that.spLang, that.spEngine);
                }

                return this.promise;
            },
            showResponse: function(response){
                this.bubbleText = response;
            },
            stopSpeaking: function() {
                window.stopSpeech();
            },
            triggerSpeechRecognition: function() {
                // rec.listen();
            },
            forceResize: function () {
                //window.dynamicResize(this.width * 1.5, this.height);
                window.dynamicResize(this.width, this.height);
            },
            endTele: function () {
                // tele.endTele();
            },
            closeOverlay: function () {
                window.overlayClose();
            },
            startOverlay: function () {
                window.overlayOpen('max',1);
            },
            telle: function () {
                this.speak("Hang on! I am trying to connect to the Court Clerk");
                this.tele = true;
                // tele.startTele();
            },
            teleCourt: function () {
                    const domain = 'meet.robotaisolutions.com';
                    const options = {
                        roomName: 'mv',
                        width: 1000,
                        height: 800,
                        parentNode: document.querySelector('#video-panel'),
                        interfaceConfigOverwrite: {
                            filmStripOnly: false,
                            ENABLE_DIAL_OUT: false,
                            SHOW_PROMOTIONAL_CLOSE_PAGE: true,
                            TOOLBAR_BUTTONS: [ 'tileview'],
                            TOOLBAR_ALWAYS_VISIBLE: true,
                            DISPLAY_WELCOME_PAGE_CONTENT: false,
                        }
                    };
                    const api = new JitsiMeetExternalAPI(domain, options);
                }

        },
        mounted: function () {
            //tele.startTele();

        }
    }
</script>


