window.vh_talkStarted = function() {
    clientApp.avatar.isTalking = true;
     rec.stopRec();
}

window.vh_talkEnded = function() {
    clientApp.avatar.isTalking = false;
     setTimeout(rec.startRec(),1000);
    if (clientApp.avatar.promise) {
        clientApp.avatar.promise.resolve({interrupted : false});
        clientApp.avatar.promise = null;
    }
};

let clientApp = window.clientApp = new Vue({
  router,
  vuetify,
  render: h => h(App),
    data:{
        avatar: undefined,
        popup: undefined

    },
    mounted(){
    console.log("i am mounted");
        this.avatar = this.findAvatarComponent(this);
        this.popup = this.findPopupComponent(this);
    },
    methods:{
        findAvatarComponent: function(parent) {
            let found = parent.$children.find(child => {return child.$options.name === "Avatar";});
            for (let i = 0; !found && i < parent.$children.length; i++) {
                found = this.findAvatarComponent(parent.$children[i]);
            }
            return found;
        },
        findPopupComponent: function(parent) {
            let found = parent.$children.find(child => {return child.$options.name === "popup";});
            for (let i = 0; !found && i < parent.$children.length; i++) {
                found = this.findAvatarComponent(parent.$children[i]);
            }
            return found;
        },
    }
}).$mount('#app')

