let request_as_promised = require('request-promise');

let host;
let endpoint_key;
let route;
let question;




let setQnaConsts = function() {
    host = "https://newmexicobots.azurewebsites.net/qnamaker";
    endpoint_key = "af2959b1-02ab-40fc-b371-0c1780481011";
    route = "/knowledgebases/d52a2b94-3988-4f65-a0aa-9d5115a81d55/generateAnswer";
    console.log("QNA config loaded");
};

let generateAnswer = function (question){
    queryAnswer(question)
        .then(function (result) {
            result = JSON.parse(JSON.stringify(result.answers));
            console.log("Got the answer: ", result);
            generateResponse(result[0]);
        });
};

let queryAnswer = async (transcript) => {
    try{
        question = {
            'question': transcript,
            'top': 3,
            'scoreThreshold': 20,
            'rankerType': 'QuestionOnly',
            'strictFilters': [
                {"name": "category",
                    //"value": "chatbot"
                    "value": "mv"
                }]
        };
        let options = {
            uri: host + route,
            method: 'POST',
            headers: {
                'Authorization': "EndpointKey " + endpoint_key
            },
            json: true,
            body: question

        };
        let response = await request_as_promised.post(options);
        return response;
    } catch (err){
        console.log(err.statusCode);
        console.log(err.message);
        console.log(err.error);
    }
};

